import { AgentRuntime, type AuthenticatedSession } from "./agent-runtime.js";
import type {
  AgentRuntimeConfig,
  AgentRuntimeEvent,
  AgentRuntimeEventHandler,
  AgentWalletSendTransactionRequest,
  AgentWalletSignMessageRequest,
  AgentWalletSignTransactionRequest,
  AgentWalletSignTypedDataRequest,
  AgentWalletSignedResponse,
  AgentWalletTransactionResponse,
} from "./types.js";
import type {
  FetchExecutor,
  HttpTransport,
} from "./transport/http-transport.js";
import type { StorageAdapter } from "./storage/types.js";
import type { WalletConnector, WalletMetadata } from "./wallet/base-connector.js";
import {
  ServerOrchestratorWalletConnector,
  type ServerOrchestratorWalletConnectorOptions,
} from "./wallet/server-orchestrator-connector.js";

const DEFAULT_BASE_URL = "https://lucid.daydream.systems";

export interface CreateWalletConfig {
  /**
   * Override the Lucid API base URL. Defaults to https://lucid.daydream.systems.
   */
  baseUrl?: string;
  /** Optional override for the agent reference. Falls back to `LUCID_AGENT_REF`. */
  agentRef?: string;
  /** Optional override for the agent credential ID. Falls back to `LUCID_AGENT_CREDENTIAL`. */
  credentialId?: string;
  /** Optional override for the refresh token. Falls back to `LUCID_AGENT_REFRESH_TOKEN`. */
  refreshToken?: string;
  /** Optional override for requested scopes. Falls back to parsing `LUCID_AGENT_SCOPES`. */
  scopes?: string[];
}

export interface CreateWalletOptions {
  /**
   * Optional runtime configuration overrides. Defaults are sourced from environment variables.
   */
  config?: CreateWalletConfig;

  /**
   * Optional overrides for wallet connector wiring.
   */
  wallet?: {
    /**
     * Provide a fully configured connector. When omitted, a server orchestrator connector is created.
     */
    connector?: WalletConnector;
    /**
     * Options used when bootstrapping the default server orchestrator connector.
     */
    server?: CreateWalletServerOptions;
  };

  /**
   * Inject a custom fetch implementation. Required in environments without global fetch.
   */
  fetch?: FetchExecutor;

  /**
   * Override the HTTP transport shared with the generated SDK.
   */
  transport?: HttpTransport;

  /**
   * Provide a custom storage adapter for token persistence.
   */
  storage?: StorageAdapter<string>;

  /**
   * Default headers appended to every network request.
   */
  headers?: HeadersInit;

  /**
   * Pre-register event handlers on the underlying runtime.
   */
  events?: {
    [Event in AgentRuntimeEvent]?:
      | AgentRuntimeEventHandler<Event>
      | Array<AgentRuntimeEventHandler<Event>>;
  };
}

export interface WalletClient {
  /** Underlying runtime instance for advanced scenarios. */
  readonly runtime: AgentRuntime;
  /** Wallet connector handling challenge signing. */
  readonly connector: WalletConnector;

  /** Ensure the agent is authenticated, returning the active session. */
  authenticate(): Promise<AuthenticatedSession>;
  /** Return a valid access token, refreshing or exchanging as needed. */
  ensureAccessToken(): Promise<string>;

  /** Sign an arbitrary message using the agent wallet. */
  signMessage(
    payload: AgentWalletSignMessageRequest,
  ): Promise<AgentWalletSignedResponse>;

  /** Sign typed data (EIP-712) using the agent wallet. */
  signTypedData(
    payload: AgentWalletSignTypedDataRequest,
  ): Promise<AgentWalletSignedResponse>;

  /** Sign a transaction payload without broadcasting it. */
  signTransaction(
    payload: AgentWalletSignTransactionRequest,
  ): Promise<AgentWalletSignedResponse>;

  /** Send a transaction via the agent wallet. */
  sendTransaction(
    payload: AgentWalletSendTransactionRequest,
  ): Promise<AgentWalletTransactionResponse>;

  /** Retrieve cached wallet metadata, if exposed by the connector. */
  getWalletMetadata(): Promise<WalletMetadata | null>;

  /** Resolve the primary signing address when exposed by the connector. */
  getAddress(): Promise<string | null>;

  /**
   * Subscribe to runtime events. Returns an unsubscribe handle mirroring `AgentRuntime.on`.
   */
  on<Event extends AgentRuntimeEvent>(
    event: Event,
    handler: AgentRuntimeEventHandler<Event>,
  ): () => void;

  /** Shut down the runtime, cancelling background refresh operations. */
  shutdown(): Promise<void>;
}

export const createWallet = async (
  options: CreateWalletOptions,
): Promise<WalletClient> => {
  const fetchImpl = options.fetch ?? (globalThis.fetch as FetchExecutor | undefined);
  if (!fetchImpl) {
    throw new Error(
      "createWallet was invoked without a fetch implementation. Provide one via options.fetch.",
    );
  }

  const runtimeConfig = resolveRuntimeConfig(options.config);
  const serverOverrides = options.wallet?.server ?? inferServerOverridesFromEnv();

  const connector =
    options.wallet?.connector ??
    createDefaultServerConnector({
      config: runtimeConfig,
      fetch: fetchImpl,
      server: serverOverrides,
    });

  const runtime = await AgentRuntime.fromConfig({
    config: runtimeConfig,
    wallet: { signer: connector },
    fetch: fetchImpl,
    transport: options.transport,
    storage: options.storage,
    headers: options.headers,
  });

  // Forward refreshed tokens to connectors that understand bearer tokens.
  const syncConnectorToken = (token: string | null) => {
    if (isAccessTokenAware(connector)) {
      connector.setAccessToken(token);
    }
  };

  runtime.on("authenticated", (event) => {
    syncConnectorToken(event.accessToken);
  });

  runtime.on("tokenRefreshed", (event) => {
    syncConnectorToken(event.accessToken);
  });

  if (options.events) {
    registerInitialEventHandlers(runtime, options.events);
  }

  const walletClient: WalletClient = {
    runtime,
    connector,
    authenticate: () => runtime.authenticate(),
    ensureAccessToken: () => runtime.ensureAccessToken(),
    signMessage: (payload) => runtime.api.signMessage(payload),
    signTypedData: (payload) => runtime.api.signTypedData(payload),
    signTransaction: (payload) => runtime.api.signTransaction(payload),
    sendTransaction: (payload) => runtime.api.sendTransaction(payload),
    getWalletMetadata: () => connector.getWalletMetadata(),
    getAddress: async () => {
      if (typeof connector.getAddress === "function") {
        return connector.getAddress();
      }
      const metadata = await connector.getWalletMetadata();
      return metadata?.address ?? null;
    },
    on: (event, handler) => runtime.on(event, handler),
    shutdown: () => runtime.shutdown(),
  };

  return walletClient;
};

interface DefaultServerConnectorOptions {
  config: AgentRuntimeConfig;
  server?: CreateWalletServerOptions;
  fetch: FetchExecutor;
}

const createDefaultServerConnector = (
  options: DefaultServerConnectorOptions,
): WalletConnector => {
  const { config, fetch, server } = options;
  const baseUrl = server?.baseUrl ?? config.baseUrl ?? DEFAULT_BASE_URL;

  return new ServerOrchestratorWalletConnector({
    baseUrl,
    agentRef: config.agentRef,
    fetch,
    headers: server?.headers,
    accessToken: server?.accessToken ?? null,
    authorizationContext: server?.authorizationContext,
  });
};

type CreateWalletServerOptions = Omit<
  ServerOrchestratorWalletConnectorOptions,
  "agentRef" | "fetch"
> & {
  /**
   * Override the orchestrator base URL. Defaults to the runtime base URL.
   */
  baseUrl?: string;
  /**
   * Optional initial access token applied before authentication completes.
   */
  accessToken?: string | null;
};

const isAccessTokenAware = (
  connector: WalletConnector,
): connector is WalletConnector & {
  setAccessToken(token: string | null): void;
} => typeof (connector as { setAccessToken?: unknown }).setAccessToken === "function";

const registerInitialEventHandlers = (
  runtime: AgentRuntime,
  events: NonNullable<CreateWalletOptions["events"]>,
): void => {
  for (const [event, handlers] of Object.entries(events) as Array<[
    AgentRuntimeEvent,
    CreateWalletOptions["events"][AgentRuntimeEvent],
  ]>) {
    if (!handlers) continue;
    const list = Array.isArray(handlers) ? handlers : [handlers];
    for (const handler of list) {
      runtime.on(event, handler as AgentRuntimeEventHandler<AgentRuntimeEvent>);
    }
  }
};

const resolveRuntimeConfig = (
  overrides: CreateWalletConfig | undefined,
): AgentRuntimeConfig => {
  const rawBaseUrl =
    overrides?.baseUrl ?? process.env.LUCID_BASE_URL ?? DEFAULT_BASE_URL;
  const baseUrl = rawBaseUrl.trim() || DEFAULT_BASE_URL;

  const agentRef = overrides?.agentRef ?? process.env.LUCID_AGENT_REF;
  if (!agentRef) {
    throw new Error(
      "createWallet requires an agent ref. Provide config.agentRef or set LUCID_AGENT_REF.",
    );
  }

  const credentialId = overrides?.credentialId ?? process.env.LUCID_AGENT_CREDENTIAL;
  if (!credentialId) {
    throw new Error(
      "createWallet requires an agent credential. Provide config.credentialId or set LUCID_AGENT_CREDENTIAL.",
    );
  }

  const refreshToken =
    overrides?.refreshToken ?? process.env.LUCID_AGENT_REFRESH_TOKEN ?? undefined;
  const scopes = overrides?.scopes ?? parseScopes(process.env.LUCID_AGENT_SCOPES);

  return {
    baseUrl,
    agentRef,
    credentialId,
    refreshToken,
    scopes,
  };
};

const parseScopes = (raw: string | null | undefined): string[] | undefined => {
  if (!raw) {
    return undefined;
  }

  const tokens = raw
    .split(/[,\s]+/g)
    .map((token) => token.trim())
    .filter(Boolean);

  return tokens.length ? tokens : undefined;
};

const inferServerOverridesFromEnv = (): CreateWalletServerOptions | undefined => {
  const baseUrl = process.env.LUCID_SERVER_URL;
  const accessToken = process.env.LUCID_SERVER_ACCESS_TOKEN;

  if (!baseUrl && !accessToken) {
    return undefined;
  }

  return {
    baseUrl: baseUrl ?? undefined,
    accessToken: accessToken ?? null,
  };
};
