export type AgentAuthClientErrorCode =
  | "AgentRefRequired"
  | "BaseUrlRequired"
  | "EmptyResponse"
  | "RequestFailed";

export interface AgentAuthClientErrorContext {
  agentRef?: string;
  cause?: unknown;
}

export class AgentAuthClientError extends Error {
  readonly code: AgentAuthClientErrorCode;
  readonly agentRef?: string;
  override readonly cause?: unknown;

  constructor(
    code: AgentAuthClientErrorCode,
    message: string,
    context?: AgentAuthClientErrorContext,
  ) {
    super(message);
    this.name = "AgentAuthClientError";
    this.code = code;
    this.agentRef = context?.agentRef;
    this.cause = context?.cause;
  }
}

