import { readFile } from "node:fs/promises";
import { isAbsolute, resolve } from "node:path";

import { AgentRuntimeError } from "./errors.js";
import type { AgentRuntimeConfig } from "./types.js";

const DEFAULT_CONFIG_FILENAME = ".lucid-agent.json";
const DEFAULT_API_BASE_URL = "https://api.lucid.run";

const ENV_PREFIX = "LUCID_AGENT_";

const ENV_BASE_URL = `${ENV_PREFIX}BASE_URL`;
const ENV_AGENT_REF = `${ENV_PREFIX}AGENT_REF`;
const ENV_AGENT_ID = `${ENV_PREFIX}AGENT_ID`;
const ENV_CREDENTIAL_ID = `${ENV_PREFIX}CREDENTIAL_ID`;
const ENV_REFRESH_TOKEN = `${ENV_PREFIX}REFRESH_TOKEN`;
const ENV_SCOPES = `${ENV_PREFIX}SCOPES`;
const ENV_CONFIG_PATH = `${ENV_PREFIX}CONFIG_PATH`;

export interface AgentConfigFile {
  agentId?: string;
  agentRef?: string;
  credentialId?: string;
  refreshToken?: string;
  scopes?: string[] | null;
  apiBaseUrl?: string;
  [key: string]: unknown;
}

export interface AgentConfigOverrides {
  baseUrl?: string;
  agentRef?: string;
  credentialId?: string;
  refreshToken?: string;
  scopes?: string[];
}

export interface ConfigLoaderOptions {
  /**
   * Path to the config file. Defaults to `.lucid-agent.json` in the current working directory.
   */
  path?: string;
  /**
   * Working directory used when resolving relative config paths.
   */
  cwd?: string;
  /**
   * Environment variables used for overrides. Defaults to `process.env`.
   */
  env?: Record<string, string | undefined>;
  /**
   * Runtime overrides applied last.
   */
  overrides?: AgentConfigOverrides;
  /**
   * Custom file reader for testing.
   */
  readFile?: (path: string) => Promise<string>;
}

export interface LoadedAgentRuntimeConfig {
  config: AgentRuntimeConfig;
  /**
   * Path the loader attempted to read. `undefined` when running env-only mode.
   */
  sourcePath?: string;
  /**
   * Raw parsed configuration file (if one existed).
   */
  fileConfig?: AgentConfigFile | null;
}

export async function loadAgentRuntimeConfig(
  options: ConfigLoaderOptions = {},
): Promise<LoadedAgentRuntimeConfig> {
  const env = options.env ?? process.env;
  const cwd = options.cwd ?? process.cwd();

  const explicitPath = options.path ?? env[ENV_CONFIG_PATH];
  const configPath = explicitPath ?? DEFAULT_CONFIG_FILENAME;
  const resolvedPath = isAbsolute(configPath)
    ? configPath
    : resolve(cwd, configPath);

  const fileConfig = await readConfigFile(
    resolvedPath,
    options.readFile ?? defaultReadFile,
  );

  const baseUrl =
    options.overrides?.baseUrl ??
    env[ENV_BASE_URL] ??
    fileConfig?.apiBaseUrl ??
    DEFAULT_API_BASE_URL;

  const agentRef =
    options.overrides?.agentRef ??
    env[ENV_AGENT_REF] ??
    env[ENV_AGENT_ID] ??
    fileConfig?.agentRef ??
    fileConfig?.agentId;

  const credentialId =
    options.overrides?.credentialId ??
    env[ENV_CREDENTIAL_ID] ??
    fileConfig?.credentialId;

  const refreshToken =
    options.overrides?.refreshToken ??
    env[ENV_REFRESH_TOKEN] ??
    fileConfig?.refreshToken;

  const scopes =
    normalizeScopes(options.overrides?.scopes) ??
    normalizeScopes(env[ENV_SCOPES]) ??
    normalizeScopes(fileConfig?.scopes);

  validateRequired("agentRef", agentRef, resolvedPath);
  validateRequired("credentialId", credentialId, resolvedPath);

  const config: AgentRuntimeConfig = {
    baseUrl,
    agentRef,
    credentialId,
    refreshToken: refreshToken || undefined,
    scopes: scopes ?? undefined,
  };

  return {
    config,
    sourcePath: fileConfig ? resolvedPath : undefined,
    fileConfig,
  };
}

const defaultReadFile = (path: string) => readFile(path, "utf8");

async function readConfigFile(
  path: string,
  reader: (path: string) => Promise<string>,
): Promise<AgentConfigFile | null> {
  try {
    const contents = await reader(path);
    if (!contents.trim()) {
      return {};
    }

    try {
      const parsed = JSON.parse(contents) as AgentConfigFile;
      return parsed;
    } catch (error) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        `Failed to parse config file at ${path}`,
        { cause: error },
      );
    }
  } catch (error) {
    if (
      isNodeError(error) &&
      (error.code === "ENOENT" || error.code === "ENOTDIR")
    ) {
      return null;
    }

    throw new AgentRuntimeError(
      "ConfigurationError",
      `Unable to read config file at ${path}`,
      { cause: error },
    );
  }
}

function normalizeScopes(
  value: unknown,
): string[] | undefined {
  if (!value) {
    return undefined;
  }

  if (Array.isArray(value)) {
    const scopes = value
      .filter((scope): scope is string => typeof scope === "string")
      .map((scope) => scope.trim())
      .filter(Boolean);
    return scopes.length ? scopes : undefined;
  }

  if (typeof value === "string") {
    const trimmed = value.trim();
    if (!trimmed) {
      return undefined;
    }

    try {
      const parsed = JSON.parse(trimmed);
      if (Array.isArray(parsed)) {
        return normalizeScopes(parsed);
      }
    } catch {
      // fall through to comma-separated parsing
    }

    const scopes = trimmed
      .split(",")
      .map((scope) => scope.trim())
      .filter(Boolean);
    return scopes.length ? scopes : undefined;
  }

  return undefined;
}

function validateRequired(
  field: string,
  value: string | undefined,
  path: string,
): asserts value is string {
  if (!value) {
    throw new AgentRuntimeError(
      "ConfigurationError",
      `Missing required configuration value "${field}". Checked ${path} and environment overrides.`,
    );
  }
}

function isNodeError(error: unknown): error is NodeJS.ErrnoException {
  return typeof error === "object" && error !== null && "code" in error;
}

export {
  DEFAULT_API_BASE_URL,
  DEFAULT_CONFIG_FILENAME,
  ENV_AGENT_ID,
  ENV_AGENT_REF,
  ENV_BASE_URL,
  ENV_CREDENTIAL_ID,
  ENV_REFRESH_TOKEN,
  ENV_SCOPES,
  ENV_CONFIG_PATH,
};
