import {
  HttpTransportError,
  type HttpMethod,
  type HttpRequestOptions,
  type HttpResponse,
  type HttpResponseType,
  type HttpTransport,
  type QueryParameters,
  type FetchExecutor,
} from "./http-transport.js";

export interface FetchTransportOptions {
  baseUrl?: string;
  defaultHeaders?: HeadersInit;
  defaultTimeoutMs?: number;
  fetch?: FetchExecutor;
}

interface AbortBundle {
  signal?: AbortSignal;
  cleanup?: () => void;
  timedOut(): boolean;
}

const ABSOLUTE_URL_PATTERN = /^[a-zA-Z][a-zA-Z\d+\-.]*:/;
const DEFAULT_METHOD: HttpMethod = "GET";
const DEFAULT_RESPONSE_TYPE: HttpResponseType = "json";

const resolveUrl = (
  baseUrl: string | undefined,
  input: string | URL,
  query?: QueryParameters,
): string => {
  let target: URL | null = null;

  if (input instanceof URL) {
    target = new URL(input.toString());
  } else if (ABSOLUTE_URL_PATTERN.test(input)) {
    target = new URL(input);
  } else if (baseUrl) {
    target = new URL(input, baseUrl);
  }

  if (!target) {
    const raw =
      typeof input === "string" ? input : input.toString();
    throw new HttpTransportError(
      "Failed to resolve request URL. Provide an absolute URL or configure a baseUrl.",
      {
        code: "ConfigurationError",
        url: raw,
      },
    );
  }

  if (query) {
    const params = new URLSearchParams(target.search);
    for (const [key, value] of Object.entries(query)) {
      if (value === undefined || value === null) {
        continue;
      }
      const normalized =
        typeof value === "boolean" ? (value ? "true" : "false") : String(value);
      params.set(key, normalized);
    }
    target.search = params.toString();
  }

  return target.toString();
};

const mergeHeaders = (...sources: (HeadersInit | undefined)[]): Headers => {
  const result = new Headers();
  for (const source of sources) {
    if (!source) {
      continue;
    }
    const headers = new Headers(source);
    headers.forEach((value, key) => {
      result.set(key, value);
    });
  }
  return result;
};

const createAbortBundle = (
  signal?: AbortSignal,
  timeoutMs?: number,
): AbortBundle => {
  if (timeoutMs === undefined || timeoutMs === null) {
    return {
      signal,
      cleanup: undefined,
      timedOut: () => false,
    };
  }

  const controller = new AbortController();
  let timedOut = false;
  let timeoutId: ReturnType<typeof setTimeout> | undefined;
  let abortHandler: (() => void) | undefined;

  if (signal) {
    if (signal.aborted) {
      controller.abort(getSignalReason(signal));
    } else {
      abortHandler = () => {
        if (!controller.signal.aborted) {
          controller.abort(getSignalReason(signal));
        }
      };
      signal.addEventListener("abort", abortHandler, { once: true });
    }
  }

  timeoutId = setTimeout(() => {
    if (!controller.signal.aborted) {
      timedOut = true;
      controller.abort(new Error("Request timed out"));
    }
  }, timeoutMs);

  const cleanup = () => {
    if (timeoutId) {
      clearTimeout(timeoutId);
    }
    if (signal && abortHandler) {
      signal.removeEventListener("abort", abortHandler);
    }
  };

  return {
    signal: controller.signal,
    cleanup,
    timedOut: () => timedOut,
  };
};

const getSignalReason = (signal: AbortSignal): unknown => {
  if ("reason" in signal) {
    return (signal as { reason?: unknown }).reason;
  }
  return undefined;
};

const isAbortError = (error: unknown): boolean => {
  if (typeof DOMException !== "undefined" && error instanceof DOMException) {
    return error.name === "AbortError";
  }
  return (
    error instanceof Error &&
    error.name === "AbortError"
  );
};

const parseJson = async <T>(
  response: Response,
  url: string,
): Promise<T> => {
  if (response.status === 204 || response.status === 205) {
    return null as T;
  }

  const contentLength = response.headers.get("content-length");
  if (contentLength === "0") {
    return null as T;
  }

  const text = await response.text();
  if (!text) {
    return null as T;
  }

  try {
    return JSON.parse(text) as T;
  } catch (error) {
    throw new HttpTransportError("Failed to parse JSON response", {
      code: "ParseError",
      url,
      response,
      cause: error,
    });
  }
};

const parseResponse = async <T>(
  response: Response,
  type: HttpResponseType,
  url: string,
): Promise<T> => {
  switch (type) {
    case "json":
      return parseJson<T>(response, url);
    case "text":
      return (await response.text()) as T;
    case "arrayBuffer":
      return (await response.arrayBuffer()) as T;
    case "blob":
      return (await response.blob()) as T;
    case "stream":
      return (response.body ?? null) as T;
    case "response":
      return response as unknown as T;
    default: {
      const exhaustive: never = type;
      throw new Error(`Unsupported response type: ${exhaustive}`);
    }
  }
};

export const createFetchTransport = (
  options: FetchTransportOptions = {},
): HttpTransport => {
  const fetchImpl =
    options.fetch ?? (globalThis.fetch as FetchExecutor | undefined);

  if (typeof fetchImpl !== "function") {
    throw new Error(
      "Unable to create fetch transport: no fetch implementation provided.",
    );
  }

  const baseUrl = options.baseUrl;
  const defaultHeaders = options.defaultHeaders;
  const defaultTimeoutMs = options.defaultTimeoutMs;

  const request = async <T>(
    requestOptions: HttpRequestOptions<T>,
  ): Promise<HttpResponse<T>> => {
    const method = (requestOptions.method ?? DEFAULT_METHOD).toUpperCase() as HttpMethod;

    if (
      requestOptions.json !== undefined &&
      requestOptions.body !== undefined &&
      requestOptions.body !== null
    ) {
      const raw =
        typeof requestOptions.url === "string"
          ? requestOptions.url
          : requestOptions.url.toString();
      throw new HttpTransportError(
        "Provide either `json` or `body` when issuing a request.",
        {
          code: "ConfigurationError",
          url: raw,
        },
      );
    }

    const resolvedUrl = resolveUrl(
      baseUrl,
      requestOptions.url,
      requestOptions.query,
    );
    const headers = mergeHeaders(defaultHeaders, requestOptions.headers);

    let requestBody: BodyInit | undefined;
    if (requestOptions.body !== undefined && requestOptions.body !== null) {
      requestBody = requestOptions.body;
    }

    if (requestOptions.json !== undefined) {
      requestBody = JSON.stringify(requestOptions.json);
      if (!headers.has("content-type")) {
        headers.set("content-type", "application/json");
      }
    }

    const timeoutMs =
      requestOptions.timeoutMs ?? defaultTimeoutMs;
    const abortBundle = createAbortBundle(
      requestOptions.signal,
      timeoutMs,
    );

    const fetchInit: RequestInit = {
      method,
      headers,
      signal: abortBundle.signal ?? requestOptions.signal,
    };

    if (requestBody !== undefined) {
      fetchInit.body = requestBody;
    }

    const parseAs = requestOptions.parseAs ?? DEFAULT_RESPONSE_TYPE;
    const throwOnError = requestOptions.throwOnError ?? true;

    let response: Response | undefined;

    try {
      response = await fetchImpl(resolvedUrl, fetchInit);
      const responseUrl = response.url || resolvedUrl;
      const parseTarget =
        parseAs === "response" || parseAs === "stream"
          ? response
          : response.clone();
      const data = await parseResponse<T>(parseTarget, parseAs, responseUrl);

      if (!response.ok && throwOnError) {
        throw new HttpTransportError(
          `Request to ${responseUrl} failed with status ${response.status}`,
          {
            code: "HttpError",
            url: responseUrl,
            status: response.status,
            response,
            data,
          },
        );
      }

      return {
        status: response.status,
        headers: response.headers,
        data,
        raw: response,
        url: responseUrl,
      };
    } catch (error) {
      if (error instanceof HttpTransportError) {
        throw error;
      }

      if (abortBundle.timedOut()) {
        throw new HttpTransportError("Request timed out", {
          code: "TimeoutError",
          url: response?.url ?? resolvedUrl,
          response,
          cause: error,
        });
      }

      if (isAbortError(error)) {
        throw new HttpTransportError("Request aborted", {
          code: "AbortError",
          url: response?.url ?? resolvedUrl,
          response,
          cause: error,
        });
      }

      throw new HttpTransportError("Network request failed", {
        code: "NetworkError",
        url: response?.url ?? resolvedUrl,
        response,
        cause: error,
      });
    } finally {
      abortBundle.cleanup?.();
    }
  };

  const asFetch = (): FetchExecutor => {
    const fetchFn = (async (
      input: RequestInfo | URL,
      init?: RequestInit,
    ): Promise<Response> => {
      const overrides = init ?? {};

      const headers = new Headers();
      let url: string | URL;
      let method: HttpMethod | undefined;
      let body: BodyInit | null | undefined;
      let signal: AbortSignal | undefined;

      if (input instanceof Request) {
        url = input.url;
        method = input.method as HttpMethod;
        body = input.body ?? undefined;
        signal = input.signal ?? undefined;
        new Headers(input.headers).forEach((value, key) => {
          headers.set(key, value);
        });
      } else {
        url = input;
      }

      if (overrides.headers) {
        new Headers(overrides.headers).forEach((value, key) => {
          headers.set(key, value);
        });
      }

      method =
        (overrides.method as HttpMethod | undefined) ??
        method ??
        DEFAULT_METHOD;
      if (overrides.body !== undefined) {
        body = overrides.body;
      }
      signal = overrides.signal ?? signal;

      const response = await request<Response>({
        url,
        method,
        headers,
        body: body ?? undefined,
        signal,
        parseAs: "response",
        throwOnError: false,
        timeoutMs: defaultTimeoutMs,
      });

      return response.data;
    }) as FetchExecutor;

    const globalFetch = globalThis.fetch as Partial<FetchExecutor> | undefined;
    if (globalFetch && typeof globalFetch.preconnect === "function") {
      fetchFn.preconnect = globalFetch.preconnect.bind(globalFetch);
    } else {
      const noopPreconnect: FetchExecutor["preconnect"] = async (..._args) => {};
      fetchFn.preconnect = noopPreconnect;
    }

    return fetchFn;
  };

  return {
    request,
    asFetch,
  };
};
