import { TokenManagerError } from "./errors.js";
import { MemoryStorageAdapter } from "./storage/memory-adapter.js";
import type { StorageAdapter } from "./storage/types.js";

export interface TokenBundleInput {
  accessToken: string;
  accessTokenExpiresAt: Date | string;
  refreshToken: string;
  refreshTokenExpiresAt: Date | string;
  revocationNonce?: string | null;
  scopes?: string[] | null;
}

export interface TokenSnapshot {
  accessToken: string;
  accessTokenExpiresAt: Date;
  refreshToken: string;
  refreshTokenExpiresAt: Date;
  revocationNonce?: string;
  scopes?: string[];
}

export interface CachedAccessToken {
  token: string;
  expiresAt: Date;
  scopes?: string[];
  refreshToken: string;
  refreshTokenExpiresAt: Date;
  revocationNonce?: string;
}

export interface TokenManagerOptions {
  storage?: StorageAdapter<string>;
  storageKey?: string;
  refreshLeadTimeMs?: number;
  now?: () => number;
}

export interface CancelHandle {
  cancel(): void;
}

type NormalizedTokenBundle = TokenSnapshot;

type SerializedTokenBundle = {
  version: 1;
  value: {
    accessToken: string;
    accessTokenExpiresAt: string;
    refreshToken: string;
    refreshTokenExpiresAt: string;
    revocationNonce?: string;
    scopes?: string[];
  };
};

const DEFAULT_STORAGE_KEY = "agent-auth:token-bundle";
const DEFAULT_REFRESH_LEAD_TIME_MS = 30_000;

const normalizeDate = (value: Date | string): Date =>
  value instanceof Date ? value : new Date(value);

const cloneSnapshot = (snapshot: NormalizedTokenBundle): TokenSnapshot => ({
  accessToken: snapshot.accessToken,
  accessTokenExpiresAt: new Date(snapshot.accessTokenExpiresAt.getTime()),
  refreshToken: snapshot.refreshToken,
  refreshTokenExpiresAt: new Date(snapshot.refreshTokenExpiresAt.getTime()),
  revocationNonce: snapshot.revocationNonce,
  scopes: snapshot.scopes ? [...snapshot.scopes] : undefined,
});

const serializeSnapshot = (
  snapshot: NormalizedTokenBundle,
): SerializedTokenBundle => ({
  version: 1,
  value: {
    accessToken: snapshot.accessToken,
    accessTokenExpiresAt: snapshot.accessTokenExpiresAt.toISOString(),
    refreshToken: snapshot.refreshToken,
    refreshTokenExpiresAt: snapshot.refreshTokenExpiresAt.toISOString(),
    revocationNonce: snapshot.revocationNonce,
    scopes: snapshot.scopes ? [...snapshot.scopes] : undefined,
  },
});

const deserializeSnapshot = (
  serialized: SerializedTokenBundle,
): NormalizedTokenBundle => ({
  accessToken: serialized.value.accessToken,
  accessTokenExpiresAt: normalizeDate(serialized.value.accessTokenExpiresAt),
  refreshToken: serialized.value.refreshToken,
  refreshTokenExpiresAt: normalizeDate(
    serialized.value.refreshTokenExpiresAt,
  ),
  revocationNonce: serialized.value.revocationNonce ?? undefined,
  scopes: serialized.value.scopes ? [...serialized.value.scopes] : undefined,
});

export class TokenManager {
  private readonly storage: StorageAdapter<string>;
  private readonly storageKey: string;
  private readonly refreshLeadTimeMs: number;
  private readonly now: () => number;

  private tokens: NormalizedTokenBundle | null = null;
  private refreshTimer: ReturnType<typeof setTimeout> | null = null;
  private refreshPromise: Promise<unknown> | null = null;

  constructor(options: TokenManagerOptions = {}) {
    this.storage = options.storage ?? new MemoryStorageAdapter<string>();
    this.storageKey = options.storageKey ?? DEFAULT_STORAGE_KEY;
    this.refreshLeadTimeMs =
      options.refreshLeadTimeMs ?? DEFAULT_REFRESH_LEAD_TIME_MS;
    this.now = options.now ?? (() => Date.now());
  }

  async hydrate(): Promise<TokenSnapshot | null> {
    const raw = await this.storage.get(this.storageKey);
    if (!raw) {
      return null;
    }

    let parsed: SerializedTokenBundle;
    try {
      parsed = JSON.parse(raw) as SerializedTokenBundle;
    } catch (error) {
      throw new TokenManagerError(
        "StorageFailure",
        "Failed to parse stored token bundle",
        { cause: error },
      );
    }

    if (parsed.version !== 1) {
      throw new TokenManagerError(
        "StorageFailure",
        `Unsupported token bundle version: ${parsed.version}`,
      );
    }

    this.tokens = deserializeSnapshot(parsed);
    return cloneSnapshot(this.tokens);
  }

  getSnapshot(): TokenSnapshot | null {
    return this.tokens ? cloneSnapshot(this.tokens) : null;
  }

  getCachedAccessToken(): CachedAccessToken | null {
    if (!this.tokens) {
      return null;
    }

    return {
      token: this.tokens.accessToken,
      expiresAt: new Date(this.tokens.accessTokenExpiresAt.getTime()),
      scopes: this.tokens.scopes ? [...this.tokens.scopes] : undefined,
      refreshToken: this.tokens.refreshToken,
      refreshTokenExpiresAt: new Date(
        this.tokens.refreshTokenExpiresAt.getTime(),
      ),
      revocationNonce: this.tokens.revocationNonce,
    };
  }

  async setTokens(bundle: TokenBundleInput): Promise<TokenSnapshot> {
    const normalized = this.normalizeBundle(bundle);
    this.tokens = normalized;

    const serialized = serializeSnapshot(normalized);
    await this.storage.set(this.storageKey, JSON.stringify(serialized));

    return cloneSnapshot(normalized);
  }

  async clear(): Promise<void> {
    this.tokens = null;
    this.clearScheduledRefresh();
    await this.storage.delete(this.storageKey);
  }

  scheduleRefresh(
    trigger: () => Promise<TokenBundleInput>,
  ): CancelHandle {
    if (!this.tokens) {
      throw new TokenManagerError(
        "TokensUnavailable",
        "Cannot schedule refresh without cached tokens",
      );
    }

    this.clearScheduledRefresh();

    const targetTime =
      this.tokens.accessTokenExpiresAt.getTime() - this.refreshLeadTimeMs;
    const delay = Math.max(0, targetTime - this.now());

    let cancelled = false;

    const run = () => {
      void this.withRefreshLock(async () => {
        if (cancelled) {
          return;
        }
        const next = await trigger();
        await this.setTokens(next);
      }).catch(() => {
        // swallow error so the timer promise doesn't crash the process.
      });
    };

    this.refreshTimer = setTimeout(run, delay);

    return {
      cancel: () => {
        cancelled = true;
        this.clearScheduledRefresh();
      },
    };
  }

  async withRefreshLock<T>(fn: () => Promise<T>): Promise<T> {
    if (this.refreshPromise) {
      return this.refreshPromise as Promise<T>;
    }

    const promise = (async () => {
      try {
        return await fn();
      } finally {
        this.refreshPromise = null;
      }
    })();

    this.refreshPromise = promise;
    return promise;
  }

  private normalizeBundle(bundle: TokenBundleInput): NormalizedTokenBundle {
    const accessTokenExpiresAt = normalizeDate(bundle.accessTokenExpiresAt);
    const refreshTokenExpiresAt = normalizeDate(bundle.refreshTokenExpiresAt);

    return {
      accessToken: bundle.accessToken,
      accessTokenExpiresAt,
      refreshToken: bundle.refreshToken,
      refreshTokenExpiresAt,
      revocationNonce: bundle.revocationNonce ?? undefined,
      scopes: bundle.scopes ? [...bundle.scopes] : undefined,
    };
  }

  private clearScheduledRefresh(): void {
    if (this.refreshTimer) {
      clearTimeout(this.refreshTimer);
      this.refreshTimer = null;
    }
  }
}
