import { AgentApiClient } from "./agent-api-client.js";
import { AgentAuthClient } from "./agent-auth-client.js";
import {
  loadAgentRuntimeConfig,
  type ConfigLoaderOptions,
  type LoadedAgentRuntimeConfig,
} from "./config-loader.js";
import { AgentRuntimeError } from "./errors.js";
import { TokenManager } from "./token-manager.js";
import type { TokenSnapshot } from "./token-manager.js";
import { createFetchTransport } from "./transport/fetch-transport.js";
import type {
  FetchExecutor,
  HttpTransport,
} from "./transport/http-transport.js";
import type { StorageAdapter } from "./storage/types.js";
import type {
  AgentExchangeResponse,
  AgentRuntimeConfig,
  AgentRuntimeEvent,
  AgentRuntimeEventHandler,
  AgentRuntimeEventPayloads,
  AgentRuntimeOptions,
  AgentRuntimeWallet,
} from "./types.js";

const DEFAULT_REFRESH_LEAD_TIME_MS = 30_000;

export interface AgentRuntimeFromConfigOptions {
  config: AgentRuntimeConfig;
  wallet: AgentRuntimeWallet;
  fetch?: FetchExecutor;
  transport?: HttpTransport;
  authClient?: AgentAuthClient;
  apiClient?: AgentApiClient;
  tokenManager?: TokenManager;
  storage?: StorageAdapter<string>;
  headers?: HeadersInit;
  refreshLeadTimeMs?: number;
  now?: () => number;
}

export interface AgentRuntimeLoadOptions
  extends Omit<AgentRuntimeFromConfigOptions, "config"> {
  loader?: ConfigLoaderOptions;
}

export interface AgentRuntimeLoadResult
  extends LoadedAgentRuntimeConfig {
  runtime: AgentRuntime;
}

export interface AuthenticatedSession {
  accessToken: string;
  refreshToken: string;
  expiresAt: Date;
  refreshTokenExpiresAt: Date;
  scopes?: string[];
}

type EventHandlers = {
  [Event in AgentRuntimeEvent]: Set<
    AgentRuntimeEventHandler<Event>
  >;
};

const parseDate = (value: string | Date): Date =>
  value instanceof Date ? value : new Date(value);

const resolveRefreshExpiry = (
  response: AgentExchangeResponse,
): string => {
  const candidate =
    // accommodate legacy schema name
    (response as { refresh_token_expires_at?: string })
      .refresh_token_expires_at ?? response.refresh_expires_at;
  if (!candidate) {
    throw new AgentRuntimeError(
      "AuthenticationFailed",
      "Refresh token expiry is missing in exchange response",
    );
  }
  return candidate;
};

export class AgentRuntime {
  static async fromConfig(
    options: AgentRuntimeFromConfigOptions,
  ): Promise<AgentRuntime> {
    const { config, wallet } = options;
    if (!config?.baseUrl) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        "baseUrl is required",
      );
    }
    if (!config.agentRef) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        "agentRef is required",
      );
    }
    if (!config.credentialId) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        "credentialId is required",
      );
    }
    if (!wallet?.signer) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        "wallet signer is required",
      );
    }

    const fetchImpl =
      options.fetch ?? (globalThis.fetch as FetchExecutor | undefined);
    if (!fetchImpl) {
      throw new AgentRuntimeError(
        "ConfigurationError",
        "No fetch implementation available. Provide one via options.fetch.",
      );
    }

    const refreshLeadTimeMs =
      options.refreshLeadTimeMs ?? DEFAULT_REFRESH_LEAD_TIME_MS;
    const now = options.now ?? (() => Date.now());

    const transport =
      options.transport ??
      createFetchTransport({
        baseUrl: config.baseUrl,
        fetch: fetchImpl,
        defaultHeaders: options.headers,
      });

    const authClient =
      options.authClient ??
      new AgentAuthClient({
        agentRef: config.agentRef,
        baseUrl: config.baseUrl,
        fetch: transport.asFetch(),
        headers: options.headers,
      });

    const tokenManager =
      options.tokenManager ??
      new TokenManager({
        storage: options.storage,
        refreshLeadTimeMs,
        now,
      });

    const runtime = new AgentRuntime({
      config,
      wallet,
      transport,
      authClient,
      apiClient: options.apiClient,
      tokenManager,
      refreshLeadTimeMs,
      now,
    });

    await runtime.initialize();
    return runtime;
  }

  static async load(
    options: AgentRuntimeLoadOptions,
  ): Promise<AgentRuntimeLoadResult> {
    const { loader, ...runtimeOptions } = options;
    const loadedConfig = await loadAgentRuntimeConfig(loader);

    const runtime = await AgentRuntime.fromConfig({
      ...runtimeOptions,
      config: loadedConfig.config,
    });

    return {
      ...loadedConfig,
      runtime,
    };
  }

  readonly api: AgentApiClient;

  private readonly config: AgentRuntimeConfig;
  private readonly wallet: AgentRuntimeWallet;
  private readonly transport: HttpTransport;
  private readonly authClient: AgentAuthClient;
  private readonly tokenManager: TokenManager;
  private readonly refreshLeadTimeMs: number;
  private readonly now: () => number;
  private readonly handlers: EventHandlers = {
    authenticated: new Set(),
    tokenRefreshed: new Set(),
    refreshFailed: new Set(),
    tokensCleared: new Set(),
  };

  private refreshTimer: ReturnType<typeof setTimeout> | null = null;

  private constructor(options: AgentRuntimeOptions) {
    this.config = options.config;
    this.wallet = options.wallet;
    this.transport = options.transport;
    this.authClient = options.authClient;
    this.tokenManager = options.tokenManager;
    this.refreshLeadTimeMs = options.refreshLeadTimeMs;
    this.now = options.now;
    this.api =
      options.apiClient ??
      new AgentApiClient({
        transport: this.transport,
        baseUrl: this.config.baseUrl,
        agentRef: this.config.agentRef,
        getAccessToken: async () =>
          this.tokenManager.getCachedAccessToken()?.token ?? null,
      });
  }

  async authenticate(): Promise<AuthenticatedSession> {
    const challenge = await this.authClient.requestChallenge({
      credential_id: this.config.credentialId,
      scopes: this.config.scopes,
    });

    const signature = await this.wallet.signer.signChallenge(
      challenge.challenge,
    );

    const exchange = await this.authClient.exchange({
      challenge_id: challenge.challenge.id,
      signature,
      credential_id: this.config.credentialId,
    });

    const snapshot = await this.storeTokens(exchange);
    this.emit("authenticated", snapshot);
    return snapshot;
  }

  async ensureAccessToken(): Promise<string> {
    const cached = this.tokenManager.getCachedAccessToken();
    if (!cached) {
      const session = await this.authenticate();
      return session.accessToken;
    }

    const timeToExpiry =
      cached.expiresAt.getTime() - this.now();

    if (timeToExpiry <= 0 || timeToExpiry <= this.refreshLeadTimeMs) {
      const snapshot = await this.refreshTokens();
      return snapshot.accessToken;
    }

    return cached.token;
  }

  async shutdown(): Promise<void> {
    this.clearRefreshTimer();
  }

  on<TEvent extends AgentRuntimeEvent>(
    event: TEvent,
    handler: AgentRuntimeEventHandler<TEvent>,
  ): () => void {
    const set = this.handlers[event] as Set<
      AgentRuntimeEventHandler<TEvent>
    >;
    set.add(handler);
    return () => {
      set.delete(handler);
    };
  }

  async clearTokens(reason?: string): Promise<void> {
    this.clearRefreshTimer();
    await this.tokenManager.clear();
    this.emit("tokensCleared", { reason });
  }

  private async initialize(): Promise<void> {
    const snapshot = await this.tokenManager.hydrate();
    if (snapshot) {
      this.scheduleRefresh(this.snapshotToSession(snapshot));
      return;
    }

    if (this.config.refreshToken) {
      try {
        const refreshed = await this.authClient.refresh({
          refresh_token: this.config.refreshToken,
        });
        const session = await this.storeTokens(refreshed);
        this.emit("authenticated", session);
      } catch (error) {
        this.emit("refreshFailed", { error });
      }
    }
  }

  private async refreshTokens(): Promise<AuthenticatedSession> {
    return this.tokenManager.withRefreshLock(async () => {
      const snapshot = this.tokenManager.getSnapshot();
      if (!snapshot) {
        throw new AgentRuntimeError(
          "TokensUnavailable",
          "Cannot refresh tokens without an active session",
        );
      }

      try {
        const refreshed = await this.authClient.refresh({
          refresh_token: snapshot.refreshToken,
        });
        const updated = await this.storeTokens(refreshed);
        this.emit("tokenRefreshed", updated);
        return updated;
      } catch (error) {
        this.emit("refreshFailed", { error });
        throw new AgentRuntimeError(
          "RefreshFailed",
          "Failed to refresh access token",
          { cause: error },
        );
      }
    });
  }

  private async storeTokens(
    response: AgentExchangeResponse,
  ): Promise<AuthenticatedSession> {
    const refreshTokenExpiresAt = resolveRefreshExpiry(response);
    const scoped = response.credential?.scopes ?? undefined;

    const snapshot = await this.tokenManager.setTokens({
      accessToken: response.access_token,
      accessTokenExpiresAt: parseDate(response.expires_at),
      refreshToken: response.refresh_token,
      refreshTokenExpiresAt: parseDate(refreshTokenExpiresAt),
      revocationNonce: response.credential?.revocation_nonce ?? undefined,
      scopes: scoped,
    });

    const session = this.snapshotToSession(snapshot);
    this.scheduleRefresh(session);
    return session;
  }

  private scheduleRefresh(snapshot: AuthenticatedSession): void {
    this.clearRefreshTimer();

    const target =
      snapshot.expiresAt.getTime() - this.refreshLeadTimeMs;
    const delay = Math.max(0, target - this.now());

    this.refreshTimer = setTimeout(() => {
      void this.refreshTokens().catch(() => {
        // errors are surfaced via events; background failures are swallowed.
      });
    }, delay);
  }

  private clearRefreshTimer(): void {
    if (this.refreshTimer) {
      clearTimeout(this.refreshTimer);
      this.refreshTimer = null;
    }
  }

  private emit<TEvent extends AgentRuntimeEvent>(
    event: TEvent,
    payload: AgentRuntimeEventPayloads[TEvent],
  ): void {
    const listeners = this.handlers[event] as Set<
      AgentRuntimeEventHandler<TEvent>
    >;
    for (const handler of listeners) {
      try {
        handler(payload);
      } catch {
        // ignore listener errors
      }
    }
  }

  private snapshotToSession(snapshot: TokenSnapshot): AuthenticatedSession {
    return {
      accessToken: snapshot.accessToken,
      refreshToken: snapshot.refreshToken,
      expiresAt: snapshot.accessTokenExpiresAt,
      refreshTokenExpiresAt: snapshot.refreshTokenExpiresAt,
      scopes: snapshot.scopes,
    };
  }
}
