import {
  getV1Agents,
  getV1AgentsByAgentRef,
  postV1AgentsByAgentRefWalletSendTransaction,
  postV1AgentsByAgentRefWalletSignMessage,
  postV1AgentsByAgentRefWalletSignTransaction,
  postV1AgentsByAgentRefWalletSignTypedData,
} from "@lucid-dreams/sdk";

import { AgentApiClientError } from "./errors.js";
import type {
  FetchExecutor,
  HttpTransport,
} from "./transport/http-transport.js";
import type {
  AgentApiSdkClient,
  AgentDetail,
  AgentListResponse,
  AgentWalletSendTransactionRequest,
  AgentWalletSignMessageRequest,
  AgentWalletSignedResponse,
  AgentWalletSignTransactionRequest,
  AgentWalletTransactionResponse,
  AgentWalletSignTypedDataRequest,
} from "./types.js";

export interface AgentApiClientOptions {
  transport: HttpTransport;
  /**
   * Optional base URL forwarded to the generated SDK.
   */
  baseUrl?: string;
  /**
   * Default headers attached to every request.
   */
  headers?: HeadersInit;
  /**
   * Default agent reference applied when a method doesn't provide one.
   */
  agentRef?: string;
  /**
   * Optional custom SDK client instance.
   */
  client?: AgentApiSdkClient;
  /**
   * Callback that resolves the current access token.
   */
  getAccessToken: () => Promise<string | null | undefined>;
}

export interface AgentRequestOptions {
  headers?: HeadersInit;
  signal?: AbortSignal;
  meta?: Record<string, unknown>;
}

export interface AgentScopedRequestOptions extends AgentRequestOptions {
  agentRef?: string;
}

type AgentApiOperation =
  | "listAgents"
  | "getAgent"
  | "signMessage"
  | "signTypedData"
  | "signTransaction"
  | "sendTransaction";

const DEFAULT_ERROR_MESSAGES: Record<AgentApiOperation, string> = {
  listAgents: "Failed to list agents",
  getAgent: "Failed to fetch agent details",
  signMessage: "Failed to sign message with agent wallet",
  signTypedData: "Failed to sign typed data with agent wallet",
  signTransaction: "Failed to sign transaction with agent wallet",
  sendTransaction: "Failed to send transaction with agent wallet",
};

type SuccessResult<TData> = {
  data: TData;
  error: undefined;
  request: Request;
  response: Response;
};

type ErrorResult<TError> = {
  data: undefined;
  error: TError;
  request: Request;
  response: Response;
};

type SdkResult<TData, TError> = SuccessResult<TData> | ErrorResult<TError>;

export class AgentApiClient {
  private readonly client?: AgentApiSdkClient;
  private readonly baseUrl?: string;
  private readonly defaultHeaders?: HeadersInit;
  private readonly defaultAgentRef?: string;
  private readonly fetchImpl: FetchExecutor;
  private readonly getAccessToken: () => Promise<string | null | undefined>;

  constructor(options: AgentApiClientOptions) {
    this.client = options.client;
    this.baseUrl = options.baseUrl;
    this.defaultHeaders = options.headers;
    this.defaultAgentRef = options.agentRef;
    this.fetchImpl = options.transport.asFetch();
    this.getAccessToken = options.getAccessToken;
  }

  async listAgents(options?: AgentRequestOptions): Promise<AgentListResponse> {
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await getV1Agents({
      ...sdkOptions,
    });
    return this.unwrapResult(result, { operation: "listAgents" });
  }

  async getAgent(options?: AgentScopedRequestOptions): Promise<AgentDetail> {
    const agentRef = this.resolveAgentRef(options?.agentRef);
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await getV1AgentsByAgentRef({
      ...sdkOptions,
      path: { agentRef },
    });
    return this.unwrapResult(result, { operation: "getAgent", agentRef });
  }

  async signMessage(
    payload: AgentWalletSignMessageRequest,
    options?: AgentScopedRequestOptions
  ): Promise<AgentWalletSignedResponse> {
    const agentRef = this.resolveAgentRef(options?.agentRef);
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await postV1AgentsByAgentRefWalletSignMessage({
      ...sdkOptions,
      path: { agentRef },
      body: payload,
    });
    return this.unwrapResult(result, { operation: "signMessage", agentRef });
  }

  async signTypedData(
    payload: AgentWalletSignTypedDataRequest,
    options?: AgentScopedRequestOptions
  ): Promise<AgentWalletSignedResponse> {
    const agentRef = this.resolveAgentRef(options?.agentRef);
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await postV1AgentsByAgentRefWalletSignTypedData({
      ...sdkOptions,
      path: { agentRef },
      body: payload,
    });
    return this.unwrapResult(result, {
      operation: "signTypedData",
      agentRef,
    });
  }

  async signTransaction(
    payload: AgentWalletSignTransactionRequest,
    options?: AgentScopedRequestOptions
  ): Promise<AgentWalletSignedResponse> {
    const agentRef = this.resolveAgentRef(options?.agentRef);
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await postV1AgentsByAgentRefWalletSignTransaction({
      ...sdkOptions,
      path: { agentRef },
      body: payload,
    });
    return this.unwrapResult(result, {
      operation: "signTransaction",
      agentRef,
    });
  }

  async sendTransaction(
    payload: AgentWalletSendTransactionRequest,
    options?: AgentScopedRequestOptions
  ): Promise<AgentWalletTransactionResponse> {
    const agentRef = this.resolveAgentRef(options?.agentRef);
    const sdkOptions = await this.makeBaseOptions(options);
    const result = await postV1AgentsByAgentRefWalletSendTransaction({
      ...sdkOptions,
      path: { agentRef },
      body: payload,
    });
    return this.unwrapResult(result, {
      operation: "sendTransaction",
      agentRef,
    });
  }

  private async makeBaseOptions(options?: AgentRequestOptions): Promise<{
    client?: AgentApiSdkClient;
    baseUrl?: string;
    fetch: FetchExecutor;
    headers: Record<string, string>;
    signal?: AbortSignal;
    meta?: Record<string, unknown>;
    responseStyle: "fields";
    throwOnError: false;
  }> {
    const headers = await this.buildHeaders(options?.headers);
    return {
      client: this.client,
      baseUrl: this.baseUrl,
      fetch: this.fetchImpl,
      headers,
      signal: options?.signal,
      meta: options?.meta,
      responseStyle: "fields",
      throwOnError: false,
    };
  }

  private async buildHeaders(
    overrides?: HeadersInit
  ): Promise<Record<string, string>> {
    const token = await this.getAccessToken();
    if (!token) {
      throw new AgentApiClientError(
        "AccessTokenRequired",
        "Access token is required for agent API requests"
      );
    }

    const headers = new Headers(this.defaultHeaders);
    const normalized = token.trim();
    const bearerHeader = /^bearer\s/i.test(normalized)
      ? normalized
      : `Bearer ${normalized}`;
    headers.set("authorization", bearerHeader);

    if (overrides) {
      new Headers(overrides).forEach((value, key) => {
        headers.set(key, value);
      });
    }

    return Object.fromEntries(headers.entries());
  }

  private resolveAgentRef(agentRef?: string): string {
    const value = agentRef ?? this.defaultAgentRef;
    if (!value) {
      throw new AgentApiClientError(
        "AgentRefRequired",
        "agentRef must be provided on the client or per request"
      );
    }
    return value;
  }

  private unwrapResult<TData, TError>(
    result: SdkResult<TData, TError>,
    context: { operation: AgentApiOperation; agentRef?: string }
  ): TData {
    if ("error" in result && result.error !== undefined) {
      throw new AgentApiClientError(
        "RequestFailed",
        DEFAULT_ERROR_MESSAGES[context.operation],
        {
          agentRef: context.agentRef,
          status: result.response.status,
          details: result.error,
        }
      );
    }

    if (result.data === undefined) {
      throw new AgentApiClientError(
        "EmptyResponse",
        `Received empty response for ${context.operation}`,
        {
          agentRef: context.agentRef,
          status: result.response.status,
        }
      );
    }

    return result.data;
  }
}
