import {
  postV1AuthAgentsByAgentRefChallenge,
  postV1AuthAgentsByAgentRefExchange,
  postV1AuthAgentsByAgentRefRefresh,
} from "@lucid-dreams/sdk";

import { AgentAuthClientError } from "./errors.js";
import type { FetchExecutor } from "./transport/http-transport.js";
import type {
  AgentAuthClientOptions,
  AgentAuthRequestOptions,
  AgentAuthSdkClient,
  AgentChallengeRequest,
  AgentChallengeResponse,
  AgentExchangeRequest,
  AgentExchangeResponse,
  AgentRefreshRequest,
} from "./types.js";

type AgentAuthPhase = "challenge" | "exchange" | "refresh";

type RequestConfig = {
  agentRef: string;
  headers?: HeadersInit;
  signal?: AbortSignal;
  meta?: Record<string, unknown>;
};

const DEFAULT_ERROR_MESSAGES: Record<AgentAuthPhase, string> = {
  challenge: "Failed to issue agent challenge",
  exchange: "Failed to exchange agent challenge",
  refresh: "Failed to refresh agent access token",
};

type SuccessResult<TData> = {
  data: TData;
  error: undefined;
  request: Request;
  response: Response;
};

type ErrorResult<TError> = {
  data: undefined;
  error: TError;
  request: Request;
  response: Response;
};

type SdkResult<TData, TError> = SuccessResult<TData> | ErrorResult<TError>;

export class AgentAuthClient {
  private readonly client?: AgentAuthSdkClient;
  private readonly baseUrl?: string;
  private readonly fetchImpl?: FetchExecutor;
  private readonly defaultAgentRef?: string;
  private readonly defaultHeaders?: HeadersInit;

  constructor(options: AgentAuthClientOptions) {
    const { agentRef, client, baseUrl, fetch, headers } = options;

    if (!client && !baseUrl) {
      throw new AgentAuthClientError(
        "BaseUrlRequired",
        "baseUrl is required when a client instance is not provided"
      );
    }

    this.client = client;
    this.baseUrl = baseUrl;
    this.fetchImpl = fetch;
    this.defaultAgentRef = agentRef;
    this.defaultHeaders = headers;
  }

  async requestChallenge(
    payload: AgentChallengeRequest,
    options?: AgentAuthRequestOptions
  ): Promise<AgentChallengeResponse> {
    const config = this.buildRequestConfig(options);

    try {
      const result = await postV1AuthAgentsByAgentRefChallenge({
        ...this.makeRequestOptions(config),
        body: payload,
        path: { agentRef: config.agentRef },
      });

      return this.unwrapResult(result, {
        phase: "challenge",
        agentRef: config.agentRef,
      });
    } catch (error) {
      throw this.wrapError("challenge", error, config.agentRef);
    }
  }

  async exchange(
    payload: AgentExchangeRequest,
    options?: AgentAuthRequestOptions
  ): Promise<AgentExchangeResponse> {
    const config = this.buildRequestConfig(options);

    try {
      const result = await postV1AuthAgentsByAgentRefExchange({
        ...this.makeRequestOptions(config),
        body: payload,
        path: { agentRef: config.agentRef },
      });

      return this.unwrapResult(result, {
        phase: "exchange",
        agentRef: config.agentRef,
      });
    } catch (error) {
      throw this.wrapError("exchange", error, config.agentRef);
    }
  }

  async refresh(
    payload: AgentRefreshRequest,
    options?: AgentAuthRequestOptions
  ): Promise<AgentExchangeResponse> {
    const config = this.buildRequestConfig(options);

    try {
      const result = await postV1AuthAgentsByAgentRefRefresh({
        ...this.makeRequestOptions(config),
        body: payload,
        path: { agentRef: config.agentRef },
      });

      return this.unwrapResult(result, {
        phase: "refresh",
        agentRef: config.agentRef,
      });
    } catch (error) {
      throw this.wrapError("refresh", error, config.agentRef);
    }
  }

  private buildRequestConfig(options?: AgentAuthRequestOptions): RequestConfig {
    const agentRef = this.resolveAgentRef(options?.agentRef);

    return {
      agentRef,
      headers: this.mergeHeaders(options?.headers),
      signal: options?.signal,
      meta: options?.meta,
    };
  }

  private resolveAgentRef(agentRef?: string): string {
    const value = agentRef ?? this.defaultAgentRef;
    if (!value) {
      throw new AgentAuthClientError(
        "AgentRefRequired",
        "agentRef must be provided on the client or per request"
      );
    }

    return value;
  }

  private mergeHeaders(overrides?: HeadersInit): HeadersInit | undefined {
    if (!this.defaultHeaders && !overrides) {
      return undefined;
    }

    const merged = new Headers();

    if (this.defaultHeaders) {
      new Headers(this.defaultHeaders).forEach((value, key) => {
        merged.set(key, value);
      });
    }

    if (overrides) {
      new Headers(overrides).forEach((value, key) => {
        merged.set(key, value);
      });
    }

    return Object.fromEntries(merged.entries());
  }

  private wrapError(
    phase: AgentAuthPhase,
    error: unknown,
    agentRef: string
  ): AgentAuthClientError {
    if (error instanceof AgentAuthClientError) {
      return error;
    }

    return new AgentAuthClientError(
      "RequestFailed",
      DEFAULT_ERROR_MESSAGES[phase],
      {
        agentRef,
        cause: error,
      }
    );
  }

  private makeRequestOptions(config: RequestConfig): {
    client?: AgentAuthSdkClient;
    baseUrl?: string;
    fetch?: FetchExecutor;
    headers?: HeadersInit;
    signal?: AbortSignal;
    meta?: Record<string, unknown>;
    responseStyle: "fields";
    throwOnError: false;
  } {
    return {
      client: this.client,
      baseUrl: this.baseUrl,
      fetch: this.fetchImpl,
      headers: config.headers,
      signal: config.signal,
      meta: config.meta,
      responseStyle: "fields",
      throwOnError: false,
    };
  }

  private unwrapResult<TData, TError>(
    result: SdkResult<TData, TError>,
    context: { phase: AgentAuthPhase; agentRef: string }
  ): TData {
    if ("error" in result && result.error !== undefined) {
      throw new AgentAuthClientError(
        "RequestFailed",
        DEFAULT_ERROR_MESSAGES[context.phase],
        {
          agentRef: context.agentRef,
          cause: {
            error: result.error,
            response: result.response,
            request: result.request,
          },
        }
      );
    }

    if (result.data === undefined) {
      throw new AgentAuthClientError(
        "EmptyResponse",
        `Empty ${context.phase} response`,
        {
          agentRef: context.agentRef,
          cause: {
            response: result.response,
            request: result.request,
          },
        }
      );
    }

    return result.data;
  }
}
