import { createFetchTransport } from "./fetch-transport.js";
import type { FetchTransportOptions } from "./fetch-transport.js";
import type {
  FetchExecutor,
  HttpTransport,
} from "./http-transport.js";

export interface UndiciTransportOptions extends FetchTransportOptions {
  /**
   * Optional Undici dispatcher instance injected into each request.
   */
  dispatcher?: unknown;
}

export const createUndiciTransport = (
  options: UndiciTransportOptions = {},
): HttpTransport => {
  const { dispatcher, fetch: fetchOption, ...rest } = options;

  if (!dispatcher) {
    return createFetchTransport({
      ...rest,
      fetch: fetchOption,
    });
  }

  const baseFetch =
    (fetchOption as typeof globalThis.fetch | undefined) ??
    (globalThis.fetch as typeof globalThis.fetch | undefined);

  if (typeof baseFetch !== "function") {
    throw new Error(
      "Unable to create undici transport: provide the undici fetch implementation.",
    );
  }

  const fetchWithDispatcher = ((
    input: Parameters<FetchExecutor>[0],
    init?: Parameters<FetchExecutor>[1],
  ) =>
    baseFetch(input, {
      ...(init ?? {}),
      dispatcher,
    } as RequestInit)) as typeof globalThis.fetch;

  if ("preconnect" in baseFetch) {
    const preconnect = (
      baseFetch as typeof globalThis.fetch & {
        preconnect?: (...args: unknown[]) => Promise<void>;
      }
    ).preconnect;
    if (typeof preconnect === "function") {
      (
        fetchWithDispatcher as typeof globalThis.fetch & {
          preconnect?: typeof preconnect;
        }
      ).preconnect = preconnect.bind(baseFetch);
    }
  }

  return createFetchTransport({
    ...rest,
    fetch: fetchWithDispatcher as FetchExecutor,
  });
};
