export type HttpMethod =
  | "GET"
  | "POST"
  | "PUT"
  | "PATCH"
  | "DELETE"
  | "HEAD"
  | "OPTIONS";

export type HttpResponseType =
  | "json"
  | "text"
  | "arrayBuffer"
  | "blob"
  | "stream"
  | "response";

export type QueryParameters = Record<
  string,
  string | number | boolean | null | undefined
>;

type FetchPreconnect = typeof fetch extends { preconnect: infer T }
  ? T
  : (input: RequestInfo | URL, init?: RequestInit) => Promise<void>;

export type FetchExecutor = ((input: RequestInfo | URL, init?: RequestInit) => Promise<Response>) & {
  /**
   * Bun extends the global fetch with a preconnect helper. The generated SDK expects it to exist,
   * so transport implementations must expose a compatible surface.
   */
  preconnect: FetchPreconnect;
};

export interface HttpRequestOptions<TResponse = unknown> {
  /**
   * Destination URL. Relative paths require a transport configured with a base URL.
   */
  url: string | URL;
  method?: HttpMethod;
  headers?: HeadersInit;
  /**
   * Raw body to send with the request.
   */
  body?: BodyInit | null;
  /**
   * Structured payload that will be JSON-encoded automatically.
   */
  json?: unknown;
  /**
   * Query parameters appended to the resolved URL.
   */
  query?: QueryParameters;
  /**
   * Abort signal provided by the caller.
   */
  signal?: AbortSignal;
  /**
   * Override response parsing strategy. Defaults to `json`.
   */
  parseAs?: HttpResponseType;
  /**
   * Maximum time in milliseconds before the transport aborts the request.
   */
  timeoutMs?: number;
  /**
   * Prevent the transport from throwing on non-2xx responses.
   */
  throwOnError?: boolean;
}

export interface HttpResponse<TData = unknown> {
  status: number;
  headers: Headers;
  data: TData;
  raw: Response;
  url: string;
}

export interface HttpTransport {
  request<TResponse = unknown>(
    options: HttpRequestOptions<TResponse>,
  ): Promise<HttpResponse<TResponse>>;
  /**
   * Returns a fetch-compatible function that honours the transport configuration.
  */
  asFetch(): FetchExecutor;
}

export type HttpTransportErrorCode =
  | "ConfigurationError"
  | "NetworkError"
  | "TimeoutError"
  | "AbortError"
  | "HttpError"
  | "ParseError";

export interface HttpTransportErrorOptions {
  code: HttpTransportErrorCode;
  url: string;
  status?: number;
  response?: Response;
  data?: unknown;
  cause?: unknown;
}

export class HttpTransportError extends Error {
  readonly code: HttpTransportErrorCode;
  readonly url: string;
  readonly status?: number;
  readonly response?: Response;
  readonly data?: unknown;
  override readonly cause?: unknown;

  constructor(message: string, options: HttpTransportErrorOptions) {
    super(message);
    this.name = "HttpTransportError";
    this.code = options.code;
    this.url = options.url;
    this.status = options.status;
    this.response = options.response;
    this.data = options.data;
    this.cause = options.cause;
  }
}

export const isHttpTransportError = (
  error: unknown,
): error is HttpTransportError => error instanceof HttpTransportError;
